<template><section>

<h1>Tooltip 文字提示</h1>
<p>常用于展示鼠标 hover 时的提示信息。</p>
<h3>基础用法</h3>
<p>可以通过以下完整示例来理解。</p>
<div class="demo-block">
  <div>
    <o-tooltip content="这里是消息提示">
      <o-button>显示Tip</o-button>
    </o-tooltip>
  </div>
</div>
<demo-block>
                        <div slot="desc"><o-tooltip content="这里是消息提示">
  <o-button>显示Tip</o-button>
</o-tooltip>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"这里是消息提示"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">o-button</span>&gt;</span>显示Tip<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">o-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<div class="demo-block">
  <div>
    <o-tooltip content="这里是消息提示">
      <o-button>Dark</o-button>
    </o-tooltip>
    <o-tooltip content="这里是消息提示" effect="light">
      <o-button>Light</o-button>
    </o-tooltip>
  </div>
</div>
<demo-block>
                        <div slot="desc"><o-tooltip content="这里是消息提示">
  <o-button>Dark</o-button>
</o-tooltip>
<o-tooltip content="这里是消息提示" effect="light">
  <o-button>Light</o-button>
</o-tooltip>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"这里是消息提示"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">o-button</span>&gt;</span>Dark<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">o-tooltip</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"这里是消息提示"</span> <span class="hljs-attr">effect</span>=<span class="hljs-string">"light"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">o-button</span>&gt;</span>Light<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">o-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>主题</h3>
<p>Tooltip 组件提供了两个不同的主题：dark 和 light。</p>
<h3>组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>消息提示文字</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>effect</td>
<td>默认提供的主题</td>
<td>string</td>
<td>dark/light</td>
<td>dark</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less" type="text/less" scoped>
</style>
<script>
  export default {
    data() {
      return {
        tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
          }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
          }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
          }]
      };
    }
  }
</script>


